import React from "react"
import s from "./SiteContainer.module.css"
const SiteContainer = (children) =>{

    const childComps = children.children

    return (
      <section>
        <div className={s['site-container']}>
          {childComps.map((child)=>{
              return child
          })}
        </div>
      </section>
    )
}

export default SiteContainer


