import React from "react"
import * as PropTypes from "prop-types"
import Footer from "../components/Footer"
import Header from "../components/Header"
import SiteNavigation from '../components/SiteNavigation'
import SiteContainer from '../components/SiteContainer'
import Transition from "../components/Transition"
import s from './LayoutStyles.module.css'


const propTypes = {
  children: PropTypes.func.isRequired,
}

class DefaultLayout extends React.Component {

  render() {
    
    return (
      <div className={s['bodyWrapper']}>
        <input type="checkbox" id="nav-trigger" className={s['navTrigger']} />
        <label htmlFor="nav-trigger" className={s['navTriggerLabel']}>
          <span className={s['navBar']}></span>
          <span className={s['navBar']}></span>
          <span className={s['navBar']}></span>
        </label>
        <div className={s['siteWrapper']}>
          <SiteNavigation />
          <SiteContainer>
            <Header />
            <Transition {...this.props}>
              {(transitionState) => {
                return this.props.children()
              }}
            </Transition>
            
            <hr  />
            <Footer />
          </SiteContainer>
        </div>
      </div>
    )
  }
}

DefaultLayout.propTypes = propTypes

export default DefaultLayout



