import React from 'react'
import GatsbyLink from 'gatsby-link'
import s from './SiteNavigation.module.css'

const closeNavigation = (e) => {
  triggerTransition('transition name');
  console.log('close nav triggered');
  let navTrig = document.querySelector('#nav-trigger')
  if(navTrig.checked) {
      navTrig.checked = false
  }
}

const triggerTransition = (name) => {
  console.log(name);
}

const mountComplete = () => {
  console.log('mount complete');
}

const SiteNavigation = () => {
  return (
    <nav className={s['siteNavigation']}>
      <ul>
        <li className={s['siteNavigationItem']}>
          <GatsbyLink className={s['navigationLink']} to="/" onClick={closeNavigation}>Home</GatsbyLink>
        </li>
        <li className={s['siteNavigationItem']}>
          <GatsbyLink className={s['navigationLink']} to="/work" onClick={closeNavigation}>Work</GatsbyLink>
        </li>
        <li className={s['siteNavigationItem']}>
          <GatsbyLink className={s['navigationLink']} to="/about" onClick={closeNavigation}>About</GatsbyLink>
        </li>
        <li className={s['siteNavigationItem']}>
          <GatsbyLink className={s['navigationLink']} to="/blog" onClick={closeNavigation}>Blog</GatsbyLink>
        </li>
      </ul>
    </nav>
  )
}

export default SiteNavigation


