const noder = {
    createElementPoolFromSelector(selector, amount){
        let element = document.querySelector(selector)
        let elementPool = []
        let i = 0;
        for(; i < amount; i++){
            elementPool.push(element.cloneNode(true))
        }
        
        return elementPool
    },

    /**
     *
     *
     * @returns - returns a reference to the parent DOM element
     *
     */
    appendChildElements(selector, elements){
        let parentEl = document.querySelector(selector)
        let i = elements.length
        
        while(i--){
            parentEl.appendChild(elements[i])
        }

        return parentEl
        
    },

    getRandomNumber(min, max){
        return Math.floor(Math.random() * (1 + max - min) + min);
    }
}

export default noder;


